<template>
  <div>
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item>采购管理</el-breadcrumb-item>
      <el-breadcrumb-item>厂家往来</el-breadcrumb-item>
      <el-breadcrumb-item class="attrbute">
        <span @click="goBack">预付冲应付</span>
      </el-breadcrumb-item>
      <el-breadcrumb-item>查看付款单</el-breadcrumb-item>
    </el-breadcrumb>
    <div class="search_container">
      <el-form :inline="true" class="demo-form-inline">
        <el-form-item label="财务组织:">
          {{params.company_name}}
        </el-form-item>
        <el-form-item label="业务日期:">
          {{params.business_time}}
        </el-form-item>

        <el-form-item label="部门:">
          {{params.department_name}}
        </el-form-item>
        <el-form-item label="单据号:">
          {{params.prepaid_sn}}
        </el-form-item>
        <el-form-item label="供应商(厂家):">
          {{params.supplier_name}}
        </el-form-item>
        <el-form-item label="审核状态:">
          {{params.auditor_status_name}}
        </el-form-item>
        <el-form-item label="备注:">
          {{params.remark}}
        </el-form-item>
      </el-form>
    </div>
    <div class="paymanageTitle">
      <i></i> <span>结算明细</span>
    </div>
    <div class="table_container">
      <el-table :highlight-current-row="true" tooltip-effect="light" border :data="addTableData" style="width: 100%" :summary-method="getSummaries" show-summary max-height="600">
        <el-table-column :show-overflow-tooltip="true" label="序号" align="center" width="46">
          <template slot-scope="scope">
            {{scope.$index+1}}
          </template>
        </el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="receivables_mode_name" label="结算方式">
          <template slot-scope="scope">
            <span>预付冲应付</span>
          </template>
        </el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="bank_name" label="银行账户">
          <template slot-scope="scope">
            <span>
              预付账户
            </span>
          </template>
        </el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="common_price" label="金额" align="right">
        </el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="remark" label="备注">
        </el-table-column>
      </el-table>
    </div>
    <div class="aboutList" v-if="is_disabled">
      <div class="paymanageTitle">
        <i></i> <span>核销明细</span>
      </div>
      <div class="table_container">
        <div>
          <el-table :highlight-current-row="true" tooltip-effect="light" border :data="instorageTable" show-summary :summary-method="getSummariesAboutlist" max-height="600">
            <el-table-column :show-overflow-tooltip="true" label="序号" align="center" width="46">
              <template slot-scope="scope">
                {{scope.$index+1}}
              </template>
            </el-table-column>
            <el-table-column :show-overflow-tooltip="true" property="bill_sn" label="单据号"></el-table-column>
            <el-table-column :show-overflow-tooltip="true" property="type_name" label="单据类型"></el-table-column>
            <el-table-column :show-overflow-tooltip="true" property="supplier_name" label="供应商(厂家)"></el-table-column>
            <el-table-column :show-overflow-tooltip="true" property="instorage_num" label="入库数量"></el-table-column>
            <el-table-column :show-overflow-tooltip="true" property="order_price" label="入库金额">
              <template slot-scope="scope">
                <pre>{{scope.row.order_price  | priceFormatNone}}</pre>
              </template>
            </el-table-column>
            <el-table-column :show-overflow-tooltip="true" property="business_time" label="业务日期" width="200"></el-table-column>
            <el-table-column :show-overflow-tooltip="true" property="add_time" label="单据时间" width="200"></el-table-column>
            <el-table-column :show-overflow-tooltip="true" property="price" label="已付款金额">
              <template slot-scope="scope">
                <pre>{{scope.row.price  | priceFormatNone}}</pre>
              </template>
            </el-table-column>
            <el-table-column :show-overflow-tooltip="true" property="rest_price" label="未付款金额">
              <template slot-scope="scope">
                <pre>{{scope.row.rest_price  | priceFormatNone}}</pre>
              </template>
            </el-table-column>
            <el-table-column :show-overflow-tooltip="true" property="bill_price" label="本次付款金额">
              <template slot-scope="scope">
                <pre>{{scope.row.bill_price  | priceFormatNone}}</pre>
              </template>
            </el-table-column>
          </el-table>
        </div>

      </div>
      <p style="margin-top: 10px;font-size: 13px;">
        注：入库金额：不包含其他费用
      </p>
    </div>
    <div slot="footer" class="dialog-footer">

      <!--未审核-->
      <template v-if="params.auditor_status==1">
        <el-button type="primary" @click="examineFun" v-if="params.auditor_status==1">审 核</el-button>
        <el-button @click="abolishFun">作 废</el-button>
      </template>
      <!-- 已审核 -->
      <template v-if="params.auditor_status == 2">
        <!-- 已审核 -->
        <el-button plain @click="auditOrUnaudit" size="mini" v-if="authority.b_nopass==1">弃审</el-button>
      </template>
      <template v-if="params.auditor_status==2 || params.auditor_status==3">
        <el-button @click="goBack">返 回</el-button>
      </template>
      <template v-else>
        <el-button @click="goBack">取 消</el-button>
      </template>
    </div>
  </div>
</template>

<script>
import { mapActions, mapGetters } from "vuex";
export default {
  name: "purchasePrepaidhandlesearch",
  data() {
    return {
      authority: {},
      addTableData: [],
      params: {},
      is_disabled: false,
      instorageTable: []
    };
  },
  computed: {
    ...mapGetters(["prepaidhandleSee_showGet"])
  },
  methods: {
    ...mapActions(["prepaidhandleDefaultIndexACT"]),
    goBack() {
      this.prepaidhandleDefaultIndexACT({
        //commont: "prepaidhandleDefault",
        goPage: this.prepaidhandleSee_showGet.goPage,
        params: this.prepaidhandleSee_showGet.params,
        pageSize: this.prepaidhandleSee_showGet.pageSize
      });
      this.$router.push({ path: "/purchase/prepaidhandle" });
    },
    examineFun(status) {
      //审核
      let payment = this.prepaidhandleSee_showGet.prepaid_id;
      this.$axMethod
        .purchaseyfAuditorPass({
          prepaid_id: payment
        })
        .then(({ data }) => {
          if (data.code == 200) {
            this.$message({
              message: "审核成功",
              type: "success"
            });
            this.goBack();
          }
        });
    },
    abolishFun() {
      //作废
      let payment = this.prepaidhandleSee_showGet.prepaid_id;
      this.$axMethod
        .purchaseyfAuditorInvalid({
          prepaid_id: payment
        })
        .then(({ data }) => {
          if (data.code == 200) {
            this.$message({
              message: "作废成功",
              type: "success"
            });
            this.goBack();
          }
        });
    },
    // 弃审
    auditOrUnaudit() {
      if (this.params.prepaid_id) {
        let prepaid_idStr = this.params.prepaid_id.toString();
        this.$axMethod
          .unAuditorPrepaid({
            prepaid_id: prepaid_idStr
          })
          .then(res => {
            if (res.data.code == 200) {
              this.$message({
                message: res.data.msg,
                type: "success"
              });
              this.goBack();
            }
          });
      } else {
        this.$message({
          message: "非法操作，请重新进入该页面",
          type: "warning"
        });
      }
    },
    getSummariesAboutlist(param) {
      const { columns, data } = param;
      const sums = [];
      columns.forEach((column, index) => {
        if (index === 0) {
          sums[index] = "总计";
          return;
        }
        const values = data.map(item => Number(item[column.property]));
        if (!values.every(value => isNaN(value))) {
          sums[index] = values.reduce((prev, curr) => {
            const value = Number(curr);
            if (!isNaN(value)) {
              return prev + curr;
            } else {
              return prev;
            }
          }, 0);
          if (index == 4) {
            sums[index] = parseFloat(sums[index]);
          } else if ([8, 9, 10].includes(index)) {
            sums[index] = parseFloat(sums[index])
              .toFixed(2)
              .toString()
              .replace(/(\d)(?=(\d{3})+(?!\d))/g, "$1" + ",");
          } else {
            sums[index] = "";
          }
        } else {
          sums[index] = "";
        }
      });

      return sums;
    },
    selmethons() {
      let obj = {
        prepaid_id: this.prepaidhandleSee_showGet.prepaid_id
      };
      this.$axMethod.purchaseGetPrepaidInfo(obj).then(({ data }) => {
        if (data.code == 200) {
          let datataple = data.data;
          this.$set(this, "params", datataple.prepaidInfo);
          this.$set(this, "addTableData", datataple.prepaidCommonList);
          this.$set(this, "instorageTable", datataple.list);
          if (datataple.list.length) {
            this.$set(this, "is_disabled", true);
          }
        }
      });
    },
    getSummaries(param) {
      const { columns, data } = param;
      const sums = [];
      columns.forEach((column, index) => {
        if (index === 0) {
          sums[index] = "总计";
          return;
        }
        const values = data.map(item => Number(item[column.property]));
        if (!values.every(value => isNaN(value))) {
          sums[index] = values.reduce((prev, curr) => {
            const value = Number(curr);
            if (!isNaN(value)) {
              return prev + curr;
            } else {
              return prev;
            }
          }, 0);
          if (index == 3) {
            sums[index] = parseFloat(sums[index])
              .toFixed(2)
              .toString()
              .replace(/(\d)(?=(\d{3})+(?!\d))/g, "$1" + ",");
          } else {
            sums[index] = "";
          }
        } else {
          sums[index] = "";
        }
      });

      return sums;
    }
  },
  created() {
    this.selmethons();
  }
};
</script>

